import {
  IonHeader,
  IonTitle,
  IonRow,
  useIonLoading,
  IonCard
} from "@ionic/react";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";

//ion files starts here
import "@ionic/react/css/core.css";
/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";
/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";
import "./statelist.css";
function StateList() {
  const history = useHistory();
  const [stateName, setStateName] = useState([]);
  const getData = async () => {
    try {
      const res = await fetch("https://api.covid19india.org/data.json");
      const data = await res.json();
      const useData = data.statewise;
      setStateName(useData);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getData();
  }, []);

  const onhandleClick = (index1) => {
    history.push(`/state/q?state=${stateName[index1].state}`);
  };
  const [present] = useIonLoading();
  return (
    <div className="statelist">
      {/* <IonHeader className="ion-padding">
        <IonToolbar>
          <IonTitle class="title" size="large">State-wise Data ( INDIA )</IonTitle>
          <IonCardSubtitle>
            Click on states to see further info including district data
          </IonCardSubtitle>
        </IonToolbar>
      </IonHeader> */}
      <IonHeader>
        <IonTitle className="state-title">
          <h1>State-wise data</h1>
        </IonTitle>
      </IonHeader>
      <IonRow>
        {stateName.map((current, index) => {
          if (index !== 0) {
            return (
              <IonCard
                className=" ion-padding ion-margin card cell-class"
                key={index}
                onClick={() => {
                  present({
                    duration: 300,
                  });
                  onhandleClick(index);
                }}
              >
                {current.state}

                {/* <IonCol className="cell-class1">{current.confirmed}</IonCol>
                <IonCol className="cell-class2">{current.deaths}</IonCol>
                <IonCol className="cell-class3">{current.active}</IonCol>
                <IonCol className="cell-class4">{current.recovered}</IonCol> */}
              </IonCard>
            );
          }
        })}
      </IonRow>
    </div>
  );
}

export default StateList;
